.MenuItem__content {
  position: absolute;
  height: 90px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.8);
}

.MenuItem__title {
  font-weight: bold;
  margin: 0px;
  font-size: 22px;
}

.MenuItem__subtitle {
  font-weight: 200;
  font-size: 16px;
}
